'use client';

import { motion } from 'framer-motion';

export default function RoadmapHeader() {
  const monthNames = [
    'Foundations',
    'Transformers & Prompting',
    'Fine-Tuning & Thesis Start',
    'Deployment & Advanced Systems',
    'Multi-Agent Systems & Thesis Finalization'
  ];

  return (
    <div className="max-w-4xl mx-auto text-center px-4 py-12">
      <motion.h1 
        initial={{ opacity: 0, y: -20 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ duration: 0.5 }}
        className="text-4xl font-bold text-text-primary mb-6"
      >
        My Learning Roadmap
      </motion.h1>

      <motion.p
        initial={{ opacity: 0, y: 20 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ duration: 0.5, delay: 0.2 }}
        className="text-text-secondary mb-12 max-w-2xl mx-auto"
      >
        A structured 5-month journey to becoming an LLM specialist through hands-on projects, 
        research, and practical skill-building.
      </motion.p>

      {/* Month overview */}
      <motion.div
        initial={{ opacity: 0 }}
        animate={{ opacity: 1 }}
        transition={{ duration: 0.5, delay: 0.4 }}
        className="grid grid-cols-1 md:grid-cols-5 gap-4 mb-16"
      >
        {monthNames.map((name, index) => (
          <div key={index} className="p-4 bg-background-darker rounded-lg">
            <div className="text-primary font-semibold mb-2">Month {index + 1}</div>
            <div className="text-text-primary">{name}</div>
          </div>
        ))}
      </motion.div>

      {/* Progress status legend */}
      <div className="flex items-center justify-center gap-4 mb-12">
        <div className="flex items-center gap-2">
          <div className="h-3 w-3 rounded-full bg-success"></div>
          <span className="text-sm text-text-secondary">Completed</span>
        </div>
        <div className="flex items-center gap-2">
          <div className="h-3 w-3 rounded-full bg-primary"></div>
          <span className="text-sm text-text-secondary">In Progress</span>
        </div>
        <div className="flex items-center gap-2">
          <div className="h-3 w-3 rounded-full bg-gray-600"></div>
          <span className="text-sm text-text-secondary">Not Started</span>
        </div>
      </div>
    </div>
  );
} 