'use client';

import { motion } from 'framer-motion';
import { roadmapData } from '@/data/roadmap';
import RoadmapItem from './RoadmapItem';

export default function RoadmapTimeline() {
  // Group roadmap items by month
  const monthGroups = roadmapData.reduce((groups, item) => {
    const month = item.month;
    if (!groups[month]) {
      groups[month] = [];
    }
    groups[month].push(item);
    return groups;
  }, {} as Record<string, typeof roadmapData>);

  const months = Object.keys(monthGroups);

  return (
    <div className="max-w-6xl mx-auto px-4 py-12">
      <div className="relative">
        {/* Vertical timeline line */}
        <div className="absolute left-3 md:left-1/2 top-0 bottom-0 w-px bg-gray-700 transform md:translate-x-0" />
        
        {/* Timeline items */}
        <div className="relative">
          {roadmapData.map((item, index) => (
            <RoadmapItem key={item.id} item={item} index={index} />
          ))}
        </div>
      </div>
    </div>
  );
} 